.search-container{
    background-color: rgba(0, 0, 0, 0.85);
    text-align: center;
    line-height: 40px;
}
.search-container input{
    width: 75%;
}
.shop-container{
    display: grid;
    grid-template-columns: 3fr 1fr;
}
.product-container{
    border-right: 1px solid lightgray;
    margin-right: 5px;
    margin-left: 50px;
}

.pagination{
    margin: 10px;
}

.pagination button{
        border-radius: 5px;
        margin: 10px;
        padding: 3px 16px;
        font-size: 15px;

    }

.pagination .selected{
    background-color: blue;
    color: white;
}